import React from 'react'
import { Formik, Form, Field } from 'formik'
import * as Yup from 'yup'

const PopupCreateMark = ({ coordinates, createMarkHandler, showPopupHandler }) => {

   const schema = Yup.object({
      hint: Yup.string()
         .min(3, 'Слишком короткое название!')
         .max(50, 'Слишком длинное название!')
         .required('Обязательное поле!'),
      balloon: Yup.string()
         .max(80, 'Максимум 80 символов'),
      markType: Yup.string()
         .required('Выберите тип метки')
   })

   return (
      <>
         <Formik
            initialValues={{
               hint: '',
               balloon: '',
               markType: ''
            }}
            validationSchema={schema}
            onSubmit={values => {
               createMarkHandler([coordinates[0], coordinates[1]], values.hint, values.balloon, values.markType)
               showPopupHandler(0)
            }}
         >
            {({ errors, touched }) => (
               <Form className="form-create-mark">
                  <div className="form-header">
                     <p className="text-center">Создание новой метки</p>
                  </div>
                  <Field
                     type="text"
                     id="hint"
                     name="hint"
                     placeholder="Название метки"
                  />
                  {errors.hint && touched.hint ? (
                     <div className="form-error">{errors.hint}</div>
                  ) : null}
                  <Field
                     component="textarea"
                     type="text"
                     id="balloon"
                     name="balloon"
                     placeholder="Описание метки"
                     rows="4"
                  />
                  <p style={{ textAlign: 'center' }}>Тип метки:</p>
                  <div role="group" className="form-radio">
                     <div>
                        <label>
                           <Field
                              type="radio"
                              name="markType"
                              value='fire'
                           />
                           <span>Пожар</span></label>
                     </div>
                     <div>
                        <label>
                           <Field
                              type="radio"
                              name="markType"
                              value='firefighters'
                           />
                           <span>Пожарный расчёт</span></label>
                     </div>
                  </div>
                  {errors.markType ? (
                     <div className="form-error text-center">{errors.markType}</div>)
                     : null
                  }
                  <button className="button button-primary" type="submit">Создать</button>
               </Form>
            )}
         </Formik>
      </>
   )
}

export default PopupCreateMark