import React from 'react'
import { Placemark } from 'react-yandex-maps'

const Placemarks = (props) => {

   const setContextToPlacemark = (e, id) => {
      props.setPlacemarkId(id)
      props.setContextType('placemark')
      e.stopPropagation()
   }

   function getArray(array) {
      return array.map(i => {
         return (
            <Placemark
               key={i.id}
               onContextMenu={(e) => setContextToPlacemark(e, i.id)}
               geometry={i.coordinates}
               properties={{ hintContent: i.properties.hint || null, balloonContent: i.properties.balloon || null }}
               options={{ iconLayout: 'default#image', iconImageHref: i.icon || null, draggable: i.draggable }}
               modules={['geoObject.addon.hint', 'geoObject.addon.balloon']}
               onDragEnd={e => props.setCoordinates(e.get('target').geometry.getCoordinates())}
            />
         )
      })
   }

   return (
      <>
         {props.filteredMarks.length > 0
            ? getArray(props.filteredMarks)
            : getArray(props.totalMarks)
         }
      </>
   )
}

export default Placemarks