import React, { useState, useEffect, useRef } from 'react'
import Fuse from 'fuse.js'


const FilterByText = (props) => {
   const [selected, setSelected] = useState(false)
   const [text, setText] = useState('')
   // const searchIcon = <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24"><path d="M21.172 24l-7.387-7.387c-1.388.874-3.024 1.387-4.785 1.387-4.971 0-9-4.029-9-9s4.029-9 9-9 9 4.029 9 9c0 1.761-.514 3.398-1.387 4.785l7.387 7.387-2.828 2.828zm-12.172-8c3.859 0 7-3.14 7-7s-3.141-7-7-7-7 3.14-7 7 3.141 7 7 7z" /></svg>
   const filterInput = useRef(null)

   function checkClickOutside(event) {
      if (filterInput.current && !filterInput.current.contains(event.target)) {
         setSelected(false)
      }
   }

   useEffect(() => {
      if (!selected) return
      window.addEventListener('click', checkClickOutside)
      return () => {
         window.removeEventListener('click', checkClickOutside)
      }
   }, [selected])

   const fuse = new Fuse(props.totalMarks, {
      minMatchCharLength: 1,
      includeScore: true,
      threshold: 0.3,
      keys: ['properties.hint']
   })

   let searchResult = fuse.search(text)

   function setSearchHandler(e) {
      const value = e.currentTarget.value
      setText(value)
   }

   function goToCoords(coords) {
      props.ymaps.setCenter(coords, 8)
   }

   return (
      <div className={selected ? "filter__name filter-name-white" : "filter__name"}>
         <input
            ref={filterInput}
            className="filter-name-search"
            type="search"
            placeholder="Поиск по имени"
            value={props.filteredText}
            onChange={setSearchHandler}
            onFocus={() => setSelected(true)}
         />
         {/* <i className="search-icon">{searchIcon}</i> */}
         <div className={selected ? "filter__suggests" : "filter__suggests hidden"}>
            <ul className="filter-suggests-list">
               {searchResult.length > 0
                  ? searchResult.map((item, index) => {
                     return (
                        <li key={index} onClick={() => goToCoords(item.item.coordinates)}>{item.item.properties.hint}</li>
                     )
                  })
                  : props.totalMarks.map((item, index) => {
                     return (
                        <li key={index} onClick={() => goToCoords(item.coordinates)}>{item.properties.hint}</li>
                     )
                  })}
            </ul>
         </div>
      </div>
   )
}

export default FilterByText