import React from 'react'

const PopupDeleteMark = ({ deleteMarkHandler, showPopupHandler, placemarkId }) => {

   const deleteMark = () => {
      deleteMarkHandler(placemarkId)
      showPopupHandler(false)
   }

   return (
      <div className="popup__delete">
         <div>
            <h2>Вы уверены что хотите удалить эту метку?</h2>
            <p>Это действие нельзя отменить.</p>
         </div>
         <div className="popup__buttons">
            <button className="button button-success" onClick={deleteMark}>Да</button>
            <button className="button button-danger" onClick={() => showPopupHandler(false)}>Нет</button>
         </div>
      </div>
   )
}

export default PopupDeleteMark