import { connect } from "react-redux"
import {
   showContextMenuHandler,
   showPopupHandler,
   setCoordinates,
   setPlacemarkId,
   setContextType,
   setPopupContent,
   editMarkPosition,
   createMarkHandler,
   deleteMarkHandler,
   showSidebar,
   getFilterByText,
   getFilterByType,
   editMarkInfo
} from "../../redux/mapReducer"
import MainMap from "./MainMap"

let mapStateToProps = (state) => {
   return {
      mapPage: state.mapPage,
      auth: state.auth
   }
}

let mapDispatchToProps = (dispatch) => {
   return {
      contextMenuHandler: (showContextMenu) => {
         dispatch(showContextMenuHandler(showContextMenu))
      },
      showPopupHandler: (showPopup) => {
         dispatch(showPopupHandler(showPopup))
      },
      setPlacemarkId: (id) => {
         dispatch(setPlacemarkId(id))
      },
      setCoordinates: (coordinates) => {
         dispatch(setCoordinates(coordinates))
      },
      setContextType: (contextType) => {
         dispatch(setContextType(contextType))
      },
      setPopupContent: (contentType) => {
         dispatch(setPopupContent(contentType))
      },
      editMarkPosition: (id, drag, param, value) => {
         dispatch(editMarkPosition(id, drag, param, value))
      },
      createMarkHandler: (coordinates, hint, balloon, markType) => { // Создание новой метки
         dispatch(createMarkHandler(coordinates, hint, balloon, markType))
      },
      deleteMarkHandler: (id) => { // Удаление метки
         dispatch(deleteMarkHandler(id))
      },
      editMarkInfoHandler: (id, values) => { // Редактирование информации метки
         dispatch(editMarkInfo(id, values))
      },
      showSidebar: () => {
         dispatch(showSidebar())
      },
      getFilterByText: (text) => {
         dispatch(getFilterByText(text))
      },
      getFilterByType: (filter) => {
         dispatch(getFilterByType(filter))
      }
   }
}

const MapContainer = connect(mapStateToProps, mapDispatchToProps)(MainMap)

export default MapContainer