import React from 'react'
import PopupCreateMark from '../PopupContent/PopupCreateMark'
import PopupEditMark from '../PopupContent/PopupEditMark'
import PopupDeleteMark from '../PopupContent/PopupDeleteMark'

const Popup = (props) => {

   const markNames = {
      create: 'create',
      edit: 'edit',
      delete: 'delete'
   }

   const closePopup = () => {
      props.showPopupHandler(false)
   }

   return (
      <div className={`popup ${props.showPopup ? 'active' : null}`}>
         <div className="popup__close" onClick={closePopup}>
            <span className="close-icon"></span>
         </div>
         <div className="popup__body">
            {props.popupContent === markNames.create ?
               <PopupCreateMark
                  createMarkHandler={props.createMarkHandler}
                  showPopupHandler={props.showPopupHandler}
                  coordinates={props.coordinates}
               /> : null}
            {props.popupContent === markNames.edit ?
               <PopupEditMark
                  editMarkInfoHandler={props.editMarkInfoHandler}
                  showPopupHandler={props.showPopupHandler}
                  placemarkId={props.placemarkId}
                  marks={props.marks}
               /> : null}
            {props.popupContent === markNames.delete ?
               <PopupDeleteMark
                  deleteMarkHandler={props.deleteMarkHandler}
                  showPopupHandler={props.showPopupHandler}
                  placemarkId={props.placemarkId}
               /> : null}
         </div>
      </div>
   )
}

export default Popup