const LOGIN_HANDLER = 'authReducer/LOGIN-HANDLER'
const LOGIN_SUCCESS = 'authReducer/LOGIN-SUCCESS'
const LOGOUT_HANDLER = 'authReducer/LOGOUT-HANDLER'

let initialState = {
   login: '',
   password: '',
   isLogged: false
}

const authReducer = (state = initialState, action) => {
   switch (action.type) {
      case LOGIN_HANDLER:
         return {
            ...state,
            ...action.payload
         }
      case LOGIN_SUCCESS:
         return {
            ...state,
            isLogged: action.logged
         }
      case LOGOUT_HANDLER:
         return {
            ...state,
            login: '',
            password: '',
            isLogged: false
         }
      default:
         return state
   }
}

export const setLoginData = (login, password) => ({ type: LOGIN_HANDLER, payload: { login, password } })
export const setLoginSuccess = (logged) => ({ type: LOGIN_SUCCESS, logged })
export const logout = () => ({ type: LOGOUT_HANDLER })


export const authProcess = (login, password) => (dispatch, getState) => {
   if (login === 'admin' && password === 'admin') {
      dispatch(setLoginSuccess(true))
   }
}


export default authReducer