import React from 'react'
import { Link } from 'react-router-dom'

const LogoutPage = () => {
   return (
      <div className="logout">
         <h1 className="logout__title">Вы успешно вышли из системы</h1>
         <Link to="/" className="button button-primary logout__link">Вернуться на главную</Link>
      </div>
   )
}

export default LogoutPage