import React from 'react'
import { Formik, Form, Field } from 'formik'
import * as Yup from 'yup'
import { Redirect } from 'react-router'

const LoginForm = ({ authData, authProcess }) => {

   const loginSchema = Yup.object({
      login: Yup.string()
         .required('Обязательное поле!'),
      password: Yup.string()
         .required('Обязательное поле!')
   })

   return (
      <>
         {authData.isLogged ? <Redirect to="/map" /> :
            <Formik
               initialValues={{
                  login: '',
                  password: ''
               }}
               validationSchema={loginSchema}
               onSubmit={values => {
                  authProcess(values.login, values.password)
               }}
            >
               <Form className="login-form">
                  <div>
                     <Field type="text" name="login" id="login" placeholder="Логин" />
                  </div>
                  <div>
                     <Field type="password" name="password" id="password" placeholder="Пароль" />
                  </div>
                  <div className="login-form-button">
                     <button type="submit" className="button button-primary">Войти</button>
                  </div>
               </Form>
            </Formik>
         }
      </>

   )
}

export default LoginForm