import React from 'react'

const MainPage = () => {

   return (
      <div className="app__main main">
         <div className="main__wrapper">
            <div className="main__info">
               <div className="part__heading">
                  <h2>Дисклеймер</h2>
                  <hr background="white" width="100%" height="2px" />
               </div>
               <div className="part__info">
                  <p>Это небольшое SPA приложение написано в целях тренировки с React и Redux.</p>
                  <p>За идею взята реальная задача: требовалось приложение для мониторинга пожаров, информации о пожарных расчётах с возможностью редактировать эту информацию только администратору.</p>
                  <p>Приложение условно в бета-версии, буду дорабатывать по возможности и желанию :)</p>
               </div>
               <div className="part__heading">
                  <h2>Что в данный момент реализовано</h2>
               </div>
               <div className="part__info">
                  <ul className="main-list">
                     <li><b>Админка</b>: взаимодействие с метками доступно только если вы авторизованы. Для входа в шапке нажмите "Войти". Логин и пароль - <span className="bg-s">admin</span>. После этого вы сможете взаимодействовать с картой.</li>
                     <li><b>Создание метки</b>: для создания метки щелкните ПКМ по пустому месту на карте и выберите "Создать метку". В появившемся окне вы можете выбрать название метки (которое будет использоваться в поиске), описание (поддерживаются базовые HTML теги) и тип.</li>
                     <li><b>Перемещение метки</b>: щелкнув ПКМ по уже существующей метке выберите "Переместить метку". После этого вы сможете перетащить её левой кнопкой мыши на новое место. После перетаскивания снова нажмите на неё ПКМ и выберите "Закрепить метку". Внимание! После перезагрузки страницы метка вернется на своё прежнее место.</li>
                     <li><b>Удаление метки</b>: щелкнув ПКМ по уже существующей метке выберите "Удалить метку", после чего подтвердите удаление. Внимание! После перезагрузки страницы стартовые метки снова появятся.</li>
                     <li><b>Редактирование метки</b>: щелкнув ПКМ по уже существующей метке выберите "Редактировать". Таким образом вы сможете изменить содержимое метки, а также её тип.</li>
                     <li><b>Фильтр и поиск</b>: на странице с картой имеется сайдбар с поиском и фильтрами. </li>
                     <p>Не стесняйтесь нажимать на кнопки :)</p>
                  </ul>
               </div>
               <div className="part__heading">
                  <h2>Что планируется в будущем</h2>
               </div>
               <div className="part__info">
                  <ul className="main-list">
                     <li><span className="done">Редактирование меток: возможность изменять название, описание, тип и местоположение метки; </span></li>
                     <li>Добавление информационных сообщений об успехе/неудаче той или иной операции;</li>
                     <li>Рабочая область пожарных расчётов: создание видимой территории, ответственность за которую лежит на конкретном расчёте.</li>
                  </ul>
               </div>
               <div className="part__heading">
                  <h2>Об авторе</h2>
               </div>
               <div className="part__info">
                  В данный момент <a className="link" href="https://vk.com/zero_requiem" target="_blank" rel="noreferrer" >автор</a> находится в поиске работы.
                  <p>По всем предложениям - пишите на <a className="link" href="mailto:zero_requiem@vk.com">почту.</a></p>
               </div>
            </div>
         </div>
      </div>
   )
}

export default MainPage