import React from 'react'

const FilterByType = ({ getFilterByType }) => {
   return (
      <div className="filter__type">
         <select className="filter-type" onChange={e => getFilterByType(e.target.value)}>
            <option name="filter-type-all" value='all'>Показать всё</option>
            <option name="filter-type-fire" value='fire'>Показать только пожары</option>
            <option name="filter-type-firefighters" value='firefighters'>Показать только пожарные расчёты</option>
         </select>
      </div>
   )
}

export default FilterByType