import React from 'react'

const ContextMenu = (props) => {

   const menuCreateText = 'Добавить метку'
   const menuEditText = ['Редактировать', 'Переместить метку', 'Удалить метку', 'Закрепить метку']

   const styles = {
      left: `${props.popupLocation[0]}px`,
      top: `${props.popupLocation[1]}px`
   }

   const closeContext = () => {
      props.contextMenuHandler(false)
   }

   // const [fixCoords, setFixCoords] = useState(['', '']) // Показывать ли координаты при открытии контексного меню

   // useEffect(() => {
   //    let c = props.coordinates
   //    setFixCoords([c[0].toPrecision(5), c[1].toPrecision(5)])
   // }, [props.coordinates])

   const setPopupSettings = (param) => {
      if (props.contextType === 'map' && param === 'create') {
         props.setPopupContent('create')
      } else if (props.contextType === 'placemark' && param === 'edit') {
         props.setPopupContent('edit')
      } else if (props.contextType === 'placemark' && param === 'delete') {
         props.setPopupContent('delete')
      }
      props.showPopupHandler(true)
      props.contextMenuHandler(false)
   }

   const editMarkPosition = (id, drag, param, value) => {
      props.editMarkPosition(id, drag, param, value)
      props.contextMenuHandler(false)
   }

   return (
      <div className="app__context" style={styles}>
         <ul className="context-menu">
            {/* <li>{`${fixCoords[0]}, ${fixCoords[1]}`}</li> */}
            {props.contextType === 'map'
               ? <li onClick={() => setPopupSettings('create')}>{menuCreateText}</li>
               : <>
                  {!props.editMode
                     ? <>
                        <li onClick={() => setPopupSettings('edit')}>{menuEditText[0]}</li>
                        <li onClick={() => editMarkPosition(props.placemarkId, true)}>{menuEditText[1]}</li>
                     </>
                     : <li onClick={() => editMarkPosition(props.placemarkId, false, 'newCoordinates', props.coordinates)}>{menuEditText[3]}</li>
                  }
                  <li onClick={() => setPopupSettings('delete')}>{menuEditText[2]}</li>
               </>
            }
         </ul>
         <div className="app__context-close"
            onClick={closeContext}
         >
            <span className="close-icon"></span>
         </div>
      </div>
   )
}

export default ContextMenu