import React from 'react'
import LoginForm from '../common/forms/LoginForm'

const LoginPage = ({ authData, authProcess, setLoginData }) => {
   return (
      <div className="login">
         <div className="login__container">
            <div className="login__title">
               <h2>Войдите в систему, чтобы вносить изменения</h2>
            </div>
            <div className="login__form">
               <LoginForm authData={authData} authProcess={authProcess} />
            </div>
         </div>
      </div>
   )
}
export default LoginPage