import React, { useState, useEffect } from 'react'
import { Formik, Form, Field } from 'formik'
import * as Yup from 'yup'

const PopupEditMark = ({ editMarkInfoHandler, showPopupHandler, placemarkId, marks }) => {

   useEffect(() => {
      let currentMark = marks.filter(mark => mark.id === placemarkId ? { ...mark } : null)
      setFormData({
         hint: currentMark[0].properties.hint,
         balloon: currentMark[0].properties.balloon
      })
   }, [placemarkId, marks])

   const [formData, setFormData] = useState({ hint: '', balloon: '' })

   const schema = Yup.object({
      hint: Yup.string()
         .min(3, 'Слишком короткое название!')
         .max(50, 'Слишком длинное название!')
         .required('Обязательное поле!'),
      balloon: Yup.string()
         .max(80, 'Максимум 80 символов'),
      markType: Yup.string()
         .required('Выберите тип метки')
   })

   return (
      <>
         <Formik
            enableReinitialize={true}
            initialValues={{
               hint: formData.hint,
               balloon: formData.balloon,
               markType: ''
            }}
            validationSchema={schema}
            onSubmit={values => {
               editMarkInfoHandler(placemarkId, values)
               showPopupHandler(0)
            }}
         >
            {({ errors, touched }) => (
               <Form className="form-edit-mark">
                  <div className="form-header">
                     <p className="text-center">Редактирование</p>
                  </div>
                  <Field
                     type="text"
                     id="hint"
                     name="hint"
                     placeholder="Название метки"
                  />
                  {errors.hint && touched.hint ? (
                     <div className="form-error">{errors.hint}</div>
                  ) : null}
                  <Field
                     component="textarea"
                     type="text"
                     id="balloon"
                     name="balloon"
                     placeholder="Описание метки"
                     rows="4"
                  />
                  <p style={{ textAlign: 'center' }}>Тип метки:</p>
                  <div role="group" className="form-radio">
                     <div>
                        <label>
                           <Field
                              type="radio"
                              name="markType"
                              value='fire'
                           />
                           <span>Пожар</span></label>
                     </div>
                     <div>
                        <label>
                           <Field
                              type="radio"
                              name="markType"
                              value='firefighters'
                           />
                           <span>Пожарный расчёт</span></label>
                     </div>
                  </div>
                  {errors.markType ? (
                     <div className="form-error text-center">{errors.markType}</div>)
                     : null
                  }
                  <button className="button button-primary" type="submit">Сохранить</button>
               </Form>
            )}
         </Formik>
      </>
   )
}

export default PopupEditMark