import fireIcon from '../assets/icons/fire.png'
import fireFighterIcon from '../assets/icons/firefighter.png'

const CONTEXT_MENU_HANDLER = 'mapReducer/CONTEXT-MENU-HANDLER'
const SHOW_POPUP_HANDLER = 'mapReducer/SHOW-POPUP-HANDLER'
const SET_COORDINATES = 'mapReducer/SET-COORDINATES'
const SET_CONTEXT_TYPE = 'mapReducer/SET-CONTEXT-TYPE'
const SET_POPUP_CONTENT = 'mapReducer/SET-POPUP-CONTENT'
const SET_PLACEMARK_ID = 'mapReducer/SET-PLACEMARK-ID'
const SET_NEW_MARKS_LIST = 'mapReducer/SET-NEW-MARKS-LIST'
const CREATE_MARK = 'mapReducer/CREATE-MARK'
const DELETE_MARK = 'mapReducer/DELETE-MARK'
const SHOW_SIDEBAR = 'mapReducer/SHOW-SIDEBAR'
const GET_FILTER_BY_TEXT = 'mapReducer/GET-FILTER-BY-TEXT'
const SET_FILTER_BY_TYPE = 'mapReducer/SET-FILTER-BY-TYPE'
const TOGGLE_EDIT_MODE = 'mapReducer/TOGGLE-EDIT-MODE'


let initialState = {
   marks: [
      {
         id: 1,
         coordinates: ['58.4590', '49.2570'],
         icon: fireIcon,
         markType: 'fire',
         draggable: false,
         properties: {
            hint: 'Пожар в Стрижах',
            balloon: 'Степень опасности: 7/10'
         }
      },
      {
         id: 2,
         coordinates: ['58.7229', '49.1966'],
         icon: fireFighterIcon,
         markType: 'firefighters',
         draggable: false,
         properties: {
            hint: 'Пожарная часть №42',
            balloon: '<b>Личный состав</b>: 2 бригады по 5 человек'
         }
      },
      {
         id: 3,
         coordinates: ['58.6929', '49.7597'],
         icon: fireIcon,
         markType: 'fire',
         draggable: false,
         properties: {
            hint: 'Пожар в лесу возле Кичменгского городка',
            balloon: 'Степень опасности: <b>3/10</b>'
         }
      },
      {
         id: 4,
         coordinates: ['58.4504', '49.8805'],
         icon: fireFighterIcon,
         markType: 'firefighters',
         draggable: false,
         properties: {
            hint: 'Пожарный расчёт в Кстинино',
            balloon: '<b>Личный состав</b>: 1 бригада, 7 человек'
         }
      }
   ],
   filteredMarks: [],
   contextType: '',
   popupContent: '',
   coordinates: ['', ''],
   placemarkID: null,
   showContextMenu: false,
   showPopup: false,
   filteredText: '',
   isSidebarOpen: false,
   editMode: false
}

const mapReducer = (state = initialState, action) => {
   switch (action.type) {
      case CONTEXT_MENU_HANDLER:
         return {
            ...state,
            showContextMenu: action.showContextMenu
         }
      case SHOW_POPUP_HANDLER:
         return {
            ...state,
            showPopup: action.showPopup
         }
      case SET_COORDINATES:
         return {
            ...state,
            coordinates: action.coordinates
         }
      case SET_PLACEMARK_ID:
         return {
            ...state,
            placemarkId: action.id
         }
      case SET_CONTEXT_TYPE:
         return {
            ...state,
            contextType: action.contextType
         }
      case SET_POPUP_CONTENT:
         return {
            ...state,
            popupContent: action.contentType
         }
      case SET_NEW_MARKS_LIST:
         return {
            ...state,
            marks: action.marks
         }
      case CREATE_MARK:
         return {
            ...state,
            marks: [...state.marks,
            {
               id: new Date().valueOf(),
               coordinates: [action.coordinates[0], action.coordinates[1]],
               markType: action.markType,
               icon: action.markType === 'fire' ? fireIcon : fireFighterIcon,
               properties: {
                  hint: action.hint,
                  balloon: action.balloon
               }
            }]
         }
      case DELETE_MARK:
         return {
            ...state,
            marks: state.marks.filter(item => item.id !== action.id)
         }
      case SHOW_SIDEBAR:
         return {
            ...state,
            isSidebarOpen: !state.isSidebarOpen
         }
      case TOGGLE_EDIT_MODE:
         return {
            ...state,
            editMode: !state.editMode
         }
      case GET_FILTER_BY_TEXT:
         return {
            ...state,
            filteredText: action.text
         }
      case SET_FILTER_BY_TYPE:
         return {
            ...state,
            filteredMarks: action.filteredList
         }
      default:
         return state
   }
}

export const showContextMenuHandler = (showContextMenu) => ({ type: CONTEXT_MENU_HANDLER, showContextMenu })
export const showPopupHandler = (showPopup) => ({ type: SHOW_POPUP_HANDLER, showPopup })
export const setContextType = (contextType) => ({ type: SET_CONTEXT_TYPE, contextType })
export const setPopupContent = (contentType) => ({ type: SET_POPUP_CONTENT, contentType })
export const setCoordinates = (coordinates) => ({ type: SET_COORDINATES, coordinates })
export const setPlacemarkId = (id) => ({ type: SET_PLACEMARK_ID, id })
export const setNewMarksList = (marks) => ({ type: SET_NEW_MARKS_LIST, marks })
export const toggleEditMode = () => ({ type: TOGGLE_EDIT_MODE })
export const createMarkHandler = (coordinates, hint, balloon, markType) => ({
   type: CREATE_MARK,
   coordinates,
   hint,
   balloon,
   markType
})
export const deleteMarkHandler = (id) => ({ type: DELETE_MARK, id })
export const showSidebar = () => ({ type: SHOW_SIDEBAR })
export const getFilterByText = (text) => ({ type: GET_FILTER_BY_TEXT, text })
export const setFilterByType = (filteredList) => ({ type: SET_FILTER_BY_TYPE, filteredList })


// Сейчас используются фейковые (хардкодные) данные. Создаётся фейковая строка 'newCoordinates', которая содержит новые координаты.
// С помощью этой строки я подтвердил правильность работы функции.
// В будущем, при работе с сервером, эту функцию нужно изменить так, чтобы она перезаписывала настоящие координаты при фетче.
export const editMarkPosition = (id, drag, param = 'newCoordinates', value = ['', '']) => {
   return (dispatch, getState) => {
      let currentMarks = [...getState().mapPage.marks]
      let newMarkList = currentMarks.map(mark => mark.id === id ? { ...mark, draggable: drag, [param]: value } : mark)
      dispatch(toggleEditMode())
      dispatch(setNewMarksList(newMarkList))
   }
}

export const editMarkInfo = (id, values) => {
   return (dispatch, getState) => {
      let currentMarks = [...getState().mapPage.marks]
      let newMarkList = currentMarks.map(mark => mark.id === id
         ? {
            ...mark, markType: values.markType,
            icon: values.markType === 'fire' ? fireIcon : fireFighterIcon,
            properties: { hint: values.hint, balloon: values.balloon }
         } : mark)
      dispatch(setNewMarksList(newMarkList))
   }
}

export const getFilterByType = (filter) => {
   return (dispatch, getState) => {
      let filteredList = [...getState().mapPage.marks]
      filteredList = filteredList.filter(item => item.markType === filter)

      dispatch(setFilterByType(filteredList))
   }
}

export default mapReducer