import React, { Component } from 'react'
import { YMaps, Map } from 'react-yandex-maps'

import ContextMenu from '../common/ContextMenu'
import Popup from '../common/Popup'
import Sidebar from '../Sidebar'
import Placemarks from './Placemarks'


export default class MainMap extends Component {
   constructor(props) {
      super(props);
      this.mapState = {
         center: [58.586956, 49.655350],
         zoom: 6,
         behaviors: ["drag", "scrollZoom", "multiTouch"]
      }
      this.mapOptions = {
         restrictMapArea: [[61.059, 46.287], [56.169, 53.755]],
         suppressMapOpenBlock: true,
         autoFitToViewport: 'always'
      }
      this.state = {
         ymaps: null,
         popupLocation: ['', '']
      }
      this.mapStyles = {
         position: 'absolute',
         left: 0,
         top: 0,
         width: '100%',
         height: '100%'
      }
   }
   map = React.createRef()

   componentDidMount() {
      const mapBody = document.querySelector('.map-body')
      mapBody.addEventListener('contextmenu', this.openCreateContext)
   }
   componentWillUnmount() {
      const mapBody = document.querySelector('.map-body')
      mapBody.removeEventListener('contextmenu', this.openCreateContext)
   }

   shouldShowContext = () => {
      const res = this.props.mapPage.showContextMenu === true && this.props.auth.isLogged === true
      return res
   }

   setYmaps = (ymaps) => { this.setState({ ymaps }) }

   setContextToMap = (e) => {
      if (!this.props.mapPage.showPopup) {
         let c = e.get('coords')
         this.props.setCoordinates(c)
         this.props.setContextType('map')
      }
   }

   openCreateContext = (e) => {
      if (!this.props.mapPage.showPopup) {
         const rect = document.querySelector('.map-body').getBoundingClientRect()
         const x = e.clientX - rect.left
         const y = e.clientY - rect.top
         this.setState({ popupLocation: [x, y] })
         this.props.contextMenuHandler(true)
      }
   }

   render() {
      return (
         <div className={this.props.mapPage.isSidebarOpen ? 'map w-sb' : 'map'}>
            <div className="map-sidebar">
               <Sidebar
                  ymaps={this.map}
                  totalMarks={this.props.mapPage.marks}
                  isSidebarOpen={this.props.isSidebarOpen}
                  filteredText={this.props.filteredText}
                  showSidebar={this.props.showSidebar}
                  getFilterByText={this.props.getFilterByText}
                  getFilterByType={this.props.getFilterByType}
               />
            </div>
            <div className="map-body">
               <YMaps query={{ apikey: process.env.REACT_APP_MAP_API_KEY }}>
                  <Map
                     instanceRef={(map) => this.map = map}
                     onLoad={ymaps => this.setYmaps(ymaps)}
                     onContextMenu={(e) => this.setContextToMap(e)}
                     defaultState={this.mapState}
                     defaultOptions={this.mapOptions}
                     style={this.mapStyles}
                  >
                     <Placemarks
                        setPlacemarkId={this.props.setPlacemarkId}
                        totalMarks={this.props.mapPage.marks}
                        filteredMarks={this.props.mapPage.filteredMarks}
                        setCoordinates={this.props.setCoordinates}
                        setContextType={this.props.setContextType}
                     />
                     {this.shouldShowContext() ?
                        <ContextMenu
                           placemarkId={this.props.mapPage.placemarkId}
                           coordinates={this.props.mapPage.coordinates}
                           editMode={this.props.mapPage.editMode}
                           popupLocation={this.state.popupLocation}
                           contextType={this.props.mapPage.contextType}
                           contextMenuHandler={this.props.contextMenuHandler}
                           showPopupHandler={this.props.showPopupHandler}
                           setPopupContent={this.props.setPopupContent}
                           editMarkPosition={this.props.editMarkPosition}
                           ymaps={this.map}
                        />
                        : null}
                     {this.props.mapPage.showPopup ?
                        <Popup
                           popupContent={this.props.mapPage.popupContent}
                           placemarkId={this.props.mapPage.placemarkId}
                           marks={this.props.mapPage.marks}
                           showPopup={this.props.mapPage.showPopup}
                           showPopupHandler={this.props.showPopupHandler}
                           createMarkHandler={this.props.createMarkHandler}
                           deleteMarkHandler={this.props.deleteMarkHandler}
                           editMarkInfoHandler={this.props.editMarkInfoHandler}
                           coordinates={this.props.mapPage.coordinates}
                        /> : null}
                  </Map>
               </YMaps>
            </div>
         </div >
      )
   }
}