import React from 'react'
import FilterByText from './filters/FilterByText'
import FilterByType from './filters/FilterByType'

const Sidebar = ({ ymaps, totalMarks, isSidebarOpen, showSidebar, getFilterByText, getFilterByType, filteredText }) => {

   const arrowIcon = <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm2 12l-4.5 4.5 1.527 1.5 5.973-6-5.973-6-1.527 1.5 4.5 4.5z" /></svg>

   return (
      <div className={isSidebarOpen ? 'sidebar active' : 'sidebar'}>
         <div className="sidebar__content">
            <div className="sidebar__header">
               <h2>Фильтры</h2>
            </div>
            <div className="sidebar__body">
               <div className="sidebar__filters filter">
                  <FilterByText
                     ymaps={ymaps}
                     totalMarks={totalMarks}
                     isSidebarOpen={isSidebarOpen}
                     getFilterByText={getFilterByText}
                     filteredText={filteredText}
                  />
                  <FilterByType getFilterByType={getFilterByType} />
               </div>
            </div>
         </div>
         <div className="sidebar__open" onClick={showSidebar}>
            {arrowIcon}
         </div>
      </div>
   )
}

export default Sidebar